.header {
  display: flex;
  width: 100%;
  position: relative;
  background-color: #1c1c21;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 40px;
}

.headernavigation {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  padding: 0;
  align-items: center;
  list-style-type: none;
  max-width: 1280px;
}

.headernav {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 56px;
  background: #1c1c21;
  color: #8585ad;
  text-decoration: none;
}

.headernav:nth-child(2) {
  margin-left: 49px;
  color: #8585ad;
}

.headernav:nth-child(4) {
  margin-left: 311.85px;
  color: #8585ad;
}

.link {
  margin: 0;
  padding: 0;
}

.headerlogo {
  width: 289.15px;
  height: 50px;
  margin-left: 132px;
}

.headerlogo:hover {
  cursor: pointer;
}

.icon {
  background-color: #1c1c21;
  font-family: "JetBrains";
}

.icon:hover {
  cursor: pointer;
}
