.title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: "JetBrains";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 40px;
  color: #f2f2f3;
}

.image {
  width: 480px;
  height: 240px;
  margin: 0 auto;
  padding: 0;
}

.name {
  margin: 16px 0 32px;
  padding: 0;
  font-family: "JetBrains";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
  color: #f2f2f3;
}

.values {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 0 auto;
  padding: 0;
}

.value {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 8px;
  height: 56px;
}

.nutrient {
  width: 120px;
  height: 24px;
  margin: 0;
  padding: 0;
  font-family: "JetBrains";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #8585ad;
  align-self: stretch;
}

.number {
  margin: 0 auto;
  padding: 0;
  font-family: "Iceland";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #8585ad;
  align-self: stretch;
}
