.title {
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;
  margin: 40px 0 24px 0;
  padding: 0;
  font-family: "JetBrains";
  max-width: 600px;
  width: 62.15%;
  background-color: #131316;
}

.optionscards {
  display: grid;
  grid-template-columns: repeat(2, minmax(272px, 272px));
  grid-gap: 24px;
}

.link{
  text-decoration: none;
  color: #f2f2f3;
}
