.title {
  margin: 16px 0 32px;
  font-family: "Iceland";
  font-style: normal;
  font-weight: 400;
  font-size: 144px;
  line-height: 120px;
  text-align: center;
  color: #f2f2f3;
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

.subtitle {
  font-family: "JetBrains";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
}

.check {
  margin: 60px 280px 60px 280px;
  width: 120px;
  height: 120px;
  background-image: url("../../images/graphics.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.text {
  margin-bottom: 8px;
  font-family: "JetBrains";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}
